    max-width: 25rem;
    width: 100%
}

.grid .grid-row .grid-col {
    display: block;
    flex: 0 1 1rem;
    font-size: .75rem;
    font-weight: 700;
    height: 1rem;
    line-height: 1rem;
    overflow: hidden;
    position: relative;
    text-align: center;
    width: 1rem
}

.grid .grid-row .grid-col:hover:before {
    -webkit-animation: mousehover .5s ease-in 0s 2 !important;
