.article-css {
    font-size: .667rem;
    line-height: 1.75
}

.article-css h1,
.article-css h2,
.article-css h3,
.article-css h4,
.article-css h5,
.article-css h6 {
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 24px;
    margin-top: 8px
}

.article-css h1+hr,
.article-css h2+hr,
.article-css h3+hr,
.article-css h4+hr,
.article-css h5+hr,
.article-css h6+hr {
    margin-top: -8px
}

.article-css h1 {
    font-size: 28px
}

.article-css h2 {
    font-size: 24px
}

.article-css h3 {
    font-size: 22px
}

.article-css h4 {
    font-size: 20px
}

.article-css h5 {
    font-size: 18px
}

.article-css h6 {
    font-size: 16px
}

.article-css p {
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
    margin: 0 0 32px
}

.article-css a {
    color: #fa6429;
    text-decoration: none
}

.article-css a:active,
.article-css a:focus,
.article-css a:hover {
    text-decoration: underline
}

.article-css a,
.article-css ins,
.article-css u {
    -webkit-text-decoration-skip: ink edges;
    text-decoration-skip: ink edges
}

.article-css del+del,
.article-css del+s,
.article-css ins+ins,
.article-css ins+u,
.article-css s+del,
.article-css s+s,
.article-css u+ins,
.article-css u+u {
    margin-left: .125em
}

.article-css hr {
    background: #ededed;
    border: none;
    display: block;
    height: 1px;
    margin: 8px 0 24px;
    overflow: hidden
}

.article-css hr:after {
    clear: both;
    content: "";
    display: table
}

.article-css ol,
.article-css ul {
    margin: 32px 0 32px 32px;
    padding: 0
}

.article-css ol ol,
.article-css ol ul,
.article-css ul ol,
.article-css ul ul {
    margin: 16px 0 16px 32px
}

.article-css ol li,
.article-css ul li {
    margin-top: 8px
}

.article-css ul {
    list-style: disc inside
}

.article-css ul ul {
    list-style-type: circle
}

.article-css ol {
    list-style: decimal inside
}

.article-css ol ol {
    list-style-type: lower-alpha
}

.article-css figure,
.article-css img {
    border: 0;
    height: auto;
    max-width: 100%
}

.article-css figure+em,
.article-css figure figcaption,
.article-css img+em {
    color: #a1a1a1;
    display: inline-block;
    font-size: 14px;
    text-align: center;
    width: 100%
}

.article-css figure img {
    margin: 0 auto
}

.article-css figure+figure,
.article-css figure+p {
    margin-top: 32px
}

.article-css img+figcaption {
    margin-top: 16px
}

.article-css table {
    border-collapse: collapse;
    border-spacing: 0;
    margin: 32px 0;
    text-align: left;
    width: 100%
}

.article-css table td,
.article-css table th {
    border-bottom: 1px solid #ededed;
    padding: 8px 4px
}

.article-css table th {
    border-bottom-width: 2px
}

.article-css code {
    font-family: SF Mono, Segoe UI Mono, Roboto Mono, Menlo, Courier, monospace;
    font-size: .9em;
    padding: .05em
}

.article-css pre {
    margin: 32px 0;
    overflow-x: auto
}

.article-css pre code {
    color: inherit;
    display: block;
    line-height: inherit;
    padding: 16px
}

.article-css br {
    display: none
}

.article-css blockquote {
    margin-top: 16px
}

.article-css blockquote+p {
    margin-top: 32px
}

